.page {
  scroll-behavior: smooth;

  &__menu {
    position: fixed;
    top: 0;
    right: 0;
    left: 0;

    transform: translate(-100%);
    transition: transform 1s;

    &:target {
      transform: translate(0);
    }
  }

  &__section {
    margin-bottom: 120px;
  }

  &__section-title {
    font-family: "Inter", sans-serif;
    font-style: normal;
    font-weight: 700;
    font-size: 32px;
    text-align: center;
    letter-spacing: -1.5px;
    color: #292929;

    &--left {
      text-align: left;
    }
  }
}

.adventages,
.text {
  text-align: left;
}

.adventages {
  font-family: "Inter", sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 16px;
  line-height: 140%;
  color: #292929;
  padding-bottom: 20px;
}
